export default {
	data() {
		return {
			data: [],
			isData:true,
			goodsType: '',
			itemClass: 1,
			order: 'up'
		}
	},
	onLoad: function (option) {
		this.goodsType = option.goodsType;
		this.getList();
	},
	onShow:function(){
		this.getList();
	},
	onUnload:function(){
		this.data=[],
		this.isData=true,
		this.goodsType='',
		this.itemClass= 1,
		this.order= 'up'
	},
	methods: {
		getList: function () {
			uni.showToast({icon: "loading"});
			uni.request({
				url: this.$url+'goodsList2',
				data: {goodsType: this.goodsType},
				success: (res) => {
					this.data=[];
					this.data = res.data;
				},
				fail: (err) => {
					console.log(err.errMsg)
				},
				complete:(res)=>{
					uni.hideToast();
				}
			})
		}, 
		navigateToGoods:function(e){
			uni.navigateTo({
				url: '../goods-details/goods-details?goodsID='+e,
				success:function(re){
				}
			})
		},
		changeItem: function (i) {
			uni.showToast({icon: "loading"});
			this.isData=false;
			switch (i) {
				case 1:
					this.itemClass = 1;
					switch (this.order) {
						case 'no':this.order = 'up';this.priceUp();break;
						case 'up':this.order = 'down';this.priceDown();break;
						case 'down':this.order = 'up';this.priceUp();break;
					}
					break;
				case 2:this.itemClass = 2;this.order = 'no';this.salesDown();break;
				case 3:this.itemClass = 3;this.order = 'no';this.dateDown();break;
			}
		},
		//排序
		priceUp:function(){
			setTimeout(() => {
				let res=this.data;
				let list = Object.keys(res).map(index => res[index]);
				list.sort((a, b) =>  a.price - b.price);
				this.data=list;
				uni.hideToast();
				this.isData=true;
			}, 400);
		},
		priceDown:function(){
			setTimeout(() => {
				let res=this.data;
				let list = Object.keys(res).map(index => res[index]);
				list.sort((a, b) =>  b.price - a.price);
				this.data=list;
				uni.hideToast();
				this.isData=true;
			}, 400);
		},
		salesDown:function(){
			setTimeout(() => {
				let res=this.data;
				let list = Object.keys(res).map(index => res[index]);
				list.sort((a, b) =>  b.sales - a.sales);
				this.data=list;
				uni.hideToast();
				this.isData=true;
			}, 400);
		},
		dateDown:function(){
			setTimeout(() => {
				let res=this.data;
				let list = Object.keys(res).map(index => res[index]);
				list.sort((a, b) => Date.parse(b.createDate) - Date.parse(a.createDate));
				this.data=list;
				uni.hideToast();
				this.isData=true;
			}, 400);
		}
	}
}
